import React, { useState } from "react";
import Conditions from "components/NewConditions/conditions";
import { useEffect } from "react";
import CloseCircle from "assets/icons/common/close-circle.svg";

const OrItem = ({
  filter,
  setFilter,
  index,
  conditions,
  listValue,
  setListValue,
  listOperator,
  setListOperator,
  dataDetails,
  ID,
}) => {
  const [allCondition, setAllCondition] = useState(null);
  const [valueAnd, setValueAnd] = useState(null);
  const [operatorValueAnd, setOperatorValueAnd] = useState(null);

  useEffect(() => {
    if (allCondition) {
      let tmp = [...filter];
      tmp[index] = [...allCondition];
      setFilter(tmp);
    } // eslint-disable-next-line
  }, [allCondition]);

  useEffect(() => {
    // console.log("valueAnd", valueAnd);
    if (valueAnd) {
      let tmp = [...listValue];
      tmp[index] = [...valueAnd];
      setListValue(tmp);
    } // eslint-disable-next-line
  }, [valueAnd]);

  useEffect(() => {
    // console.log("operatorValueAnd", operatorValueAnd);
    if (operatorValueAnd) {
      let tmp = [...listOperator];
      tmp[index] = [...operatorValueAnd];
      setListOperator(tmp);
    } // eslint-disable-next-line
  }, [operatorValueAnd]);

  const handleDeleteConditionsGroup = () => {
    //filter
    let tmp = [...filter];
    tmp.splice(index, 1);
    setFilter(tmp);
    //operator
    let tmpOperator = [...listOperator];
    tmpOperator.splice(index, 1);
    setListOperator(tmpOperator);
    //value
    let tmpValue = [...listValue];
    tmpValue.splice(index, 1);
    setListValue(tmpValue);
  };

  return (
    <div style={{ position: "relative" }}>
      <Conditions
        title={"AND"}
        decs={"(All conditions must be met)"}
        conditions={filter[index]}
        setConditions={setAllCondition}
        ID={ID}
        dataDetails={dataDetails}
        operatorValue={listOperator[index]}
        setOperatorValue={setOperatorValueAnd}
        value={listValue[index]}
        setValue={setValueAnd}
      />
      <img
        alt=""
        src={CloseCircle}
        style={{
          position: "absolute",
          top: "-10px",
          right: "-10px",
          cursor: "pointer",
        }}
        onClick={() => handleDeleteConditionsGroup()}
      />
    </div>
  );
};
export default OrItem;
