import { ClearOutlined, SearchOutlined } from "@ant-design/icons";
import { Input, Tag } from "antd";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import {
  loadDataLinking,
  loadHeaderLinking,
  loadPaginationLinking,
} from "redux/slices/objects";
import styled from "styled-components";
import LinkingList from "./linkingList/linkingList";

const Linking = ({
  field,
  operatorValue,
  disabled,
  conditions,
  setConditions,
  setValue,
  value,
  index,
  ID,
}) => {
  const [visible, setVisible] = useState(false);
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const dispatch = useDispatch();
  const [selectedRows, setSelectedRows] = useState([]);

  useEffect(() => {
    if (
      conditions[index].key &&
      operatorValue !== "$nin" &&
      operatorValue !== "$in"
    ) {
      setSelectedRowKeys([conditions[index].key]);
    } else if (typeof conditions[index].key === "object" && ID) {
      setSelectedRowKeys(conditions[index].key);
      setSelectedRows(value[index]);
    }
    /* eslint-disable-next-line */
  }, [conditions, index]);

  useEffect(() => {
    if (visible === true) {
      dispatch(
        loadHeaderLinking({
          object_id: field.objectname,
        })
      );
      dispatch(
        loadDataLinking({
          object_id: field.objectname,
          first_record_id: null,
          last_record_id: null,
          search_with: {
            meta: [],
            data: [],
          },
          filter: [],
        })
      );
      dispatch(
        loadPaginationLinking({
          object_id: field.objectname,
          current_page: 1,
          record_per_page: 10,
          search_with: {
            meta: [],
            data: [],
          },
          filter: [],
        })
      );
    }
    /* eslint-disable-next-line */
  }, [dispatch, visible]);

  const handleSelect = (selectedRowKeys, selectedRows) => {
    if (operatorValue !== "$nin" && operatorValue !== "$in") {
      setVisible(false);

      let tmpConditions = [...conditions];
      tmpConditions[index] = {
        ...tmpConditions[index],
        value: {
          // ...tmpConditions[index].value,
          [operatorValue]: selectedRows[0][field.field],
        },
      };
      tmpConditions[index] = {
        ...tmpConditions[index],
        key: selectedRowKeys[0],
      };
      setConditions(tmpConditions);

      let tmp = [...value];
      tmp[index] = selectedRows[0][field.field];
      setValue(tmp);
    } else {
      console.log("selectedRows", selectedRows);
      console.log("selectedRowKeys", selectedRowKeys);
      setVisible(false);

      let tmpConditions = [...conditions];

      let fieldValue = [];
      /* eslint-disable-next-line */
      selectedRows.map((item, index) => {
        if (typeof item === "object") {
          fieldValue.push(item[field.field]);
        } else {
          fieldValue.push(item);
        }
      });

      tmpConditions[index] = {
        ...tmpConditions[index],
        value: {
          // ...tmpConditions[index].value,
          [operatorValue]: [...new Set([...fieldValue])],
        },
      };

      tmpConditions[index] = {
        ...tmpConditions[index],
        key: selectedRowKeys,
      };
      setConditions(tmpConditions);

      let tmp = [...value];
      tmp[index] = [...new Set([...fieldValue])];
      setValue(tmp);
    }
  };

  useEffect(() => {
    let tmp = [];
    let tmpConditions = [];
    /*eslint-disable-next-line*/
    conditions.map((item, idx) => {
      tmpConditions.push({ ...item });
    });
    /*eslint-disable-next-line*/
    value.map((item, idx) => {
      tmp.push(item);
    });
    switch (operatorValue) {
      case "empty":
        tmpConditions[index].value = {
          $eq: null,
        };

        tmp[index] = "";
        setValue(tmp);

        setConditions(tmpConditions);
        break;
      case "not-empty":
        tmpConditions[index].value = {
          $ne: null,
        };
        tmp[index] = "";
        setValue(tmp);
        setConditions(tmpConditions);
        break;
      default:
        break;
    }

    /*eslint-disable-next-line*/
  }, [operatorValue]);

  return (
    <>
      <Input
        disabled={
          disabled || operatorValue === "empty" || operatorValue === "not-empty"
        }
        addonAfter={
          <>
            {disabled ||
            operatorValue === "empty" ||
            operatorValue === "not-empty" ||
            operatorValue === "mine" ||
            operatorValue === "not-mine" ? (
              <SearchOutlined style={{ pointerEvents: "none" }} />
            ) : (
              <SearchOutlined
                onClick={() => {
                  setVisible(true);
                }}
              />
            )}
          </>
        }
        addonBefore={
          disabled ||
          operatorValue === "empty" ||
          operatorValue === "not-empty" ||
          operatorValue === "mine" ||
          operatorValue === "not-mine" ||
          operatorValue === "$nin" ||
          operatorValue === "$in" ? (
            ""
          ) : value[index] ? (
            <ClearOutlined
              onClick={() => {
                let tmpConditions = [...conditions];

                let newTmpConditions = [];
                tmpConditions.forEach((item, idx) => {
                  newTmpConditions[idx] = { ...item };
                });

                newTmpConditions[index].value = {
                  [operatorValue]: "",
                };

                newTmpConditions[index].key = "";
                setConditions(newTmpConditions);

                let tmp = [...value];
                tmp[index] = null;
                setValue(tmp);
              }}
            />
          ) : (
            ""
          )
        }
        value={
          operatorValue === "$nin" || operatorValue === "$in"
            ? ""
            : value[index]
        }
      />
      {operatorValue === "$nin" || operatorValue === "$in"
        ? typeof value[index] === "object" &&
          value[index]?.map((item, idx) => {
            return (
              <CustomTag
                closable
                onClose={() => {
                  // let tmpRowKeys = [...selectedRowKeys];

                  let tmpConditions = [...conditions];
                  let tmp = [...value];
                  let flag;
                  // setSelectedRowKeys(tmpRowKeys);

                  tmpConditions[index] = {
                    ...tmpConditions[index],
                    value: {
                      // ...tmpConditions[index].value,
                      [operatorValue]: value[index].filter((ele, indexx) => {
                        if (ele === item) {
                          flag = indexx;
                        }
                        return ele !== item;
                      }),
                    },
                  };
                  let tempKeys =
                    typeof tmpConditions[index].key === "object"
                      ? tmpConditions[index].key
                      : [];

                  tempKeys = tempKeys.filter((ele, indexx) => indexx !== flag);
                  tmpConditions[index] = {
                    ...tmpConditions[index],
                    key: tempKeys,
                  };
                  setConditions(tmpConditions);

                  tmp[index] = value[index].filter((ele) => ele !== item);
                  setValue(tmp);
                }}
              >
                {item}
              </CustomTag>
            );
          })
        : ""}
      <LinkingList
        visible={visible}
        setVisible={setVisible}
        field={field}
        selectedRowKeys={selectedRowKeys}
        setSelectedRowKeys={setSelectedRowKeys}
        handleSelect={handleSelect}
        setSelectedRows={setSelectedRows}
        selectedRows={selectedRows}
        conditions={conditions}
        index={index}
        operatorValue={operatorValue}
        ID={ID}
      />
    </>
  );
};

export default Linking;

const CustomTag = styled(Tag)`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  text-align: center;
  width: 100%;
  overflow: auto;
  .ant-tag {
    padding: 20px;
  }
`;
