import React, { useState } from "react";
import OrItem from "./orItem";
import styled from "styled-components";
import { Button } from "antd";

const OrListCondition = ({
  filter,
  setFilter,
  listOperator,
  setListOperator,
  listValue,
  setListValue,
  ID,
  dataDetails,
}) => {
  const [deleted, setDeleted] = useState(false);

  const handleAddNewCondition = () => {
    setFilter([...filter, []]);
  };

  return (
    <div>
      {filter.map((item, idx) => {
        return (
          <OrItem
            filter={filter}
            conditions={item}
            setFilter={setFilter}
            index={idx}
            key={idx}
            deleted={deleted}
            setDeleted={setDeleted}
            listOperator={listOperator}
            listValue={listValue}
            setListValue={setListValue}
            setListOperator={setListOperator}
            dataDetails={dataDetails}
            ID={ID}
          />
        );
      })}
      <ButtonAdd onClick={() => handleAddNewCondition()}>
        + Add new condition
      </ButtonAdd>
    </div>
  );
};

export default OrListCondition;

const ButtonAdd = styled(Button)`
  cursor: pointer;
  border: none;
  background: #fff;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  text-decoration-line: underline;
  color: ${(props) => props.theme.main};
  padding: 0;
  margin-left: 19px;
  margin-top: 8px;
  margin-bottom: 16px;
  box-shadow: none;
  :hover,
  :focus,
  :active {
    color: ${(props) => props.theme.main};
    box-shadow: none;
    outline: none;
  }
`;
