import { ClearOutlined, SearchOutlined } from "@ant-design/icons";
import { Input, Tag } from "antd";
import axios from "axios";
import { BASE_URL_API } from "constants/constants";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { loadUser } from "redux/slices/objects";
import UserList from "./userList";
import { checkTokenExpiration } from "contexts/TokenCheck";
import styled from "styled-components";
const User = ({
  operatorValue,
  field,
  conditions,
  setConditions,
  setValue,
  value,
  disabled,
  index,
  ID,
  init,
}) => {
  const [visible, setVisible] = useState(false);
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  /*eslint-disable-next-line*/
  const [name, setName] = useState("");
  const [searchData, setSearchData] = useState({});
  const dispatch = useDispatch();
  const { users } = useSelector((state) => state.objectsReducer);

  useEffect(() => {
    if (visible === true) {
      dispatch(
        loadUser({
          current_page: 1,
          record_per_page: 10,
        })
      );
    }
    /* eslint-disable-next-line */
  }, [visible]);

  // console.log(selectedRowKeys, selectedRows);

  useEffect(() => {
    setName("");
    setSelectedRowKeys([]);
  }, [operatorValue]);

  useEffect(() => {
    if (!checkTokenExpiration()) return;

    if (
      conditions[index].key &&
      operatorValue !== "$nin" &&
      operatorValue !== "$in"
    ) {
      axios
        .post(
          BASE_URL_API + "user/get-user-details",
          {
            _id: conditions[index].key,
          },
          {
            headers: {
              Authorization: localStorage.getItem("setting_accessToken"),
            },
          }
        )
        .then((res) => {
          let newName = res.data.data.Full_Name;
          setName(newName);
          setVisible(false);

          let tmp = [...value];
          tmp[index] = newName;
          setValue(tmp);
        })
        .catch((err) => console.log(err));
    } else if (
      conditions[index].type === "user" &&
      (operatorValue === "$nin" || operatorValue === "$in")
    ) {
      axios
        .post(
          BASE_URL_API + "user/load-list-user",
          {
            user_ids: conditions[index].key,
          },
          {
            headers: {
              Authorization: localStorage.getItem("setting_accessToken"),
            },
          }
        )
        .then((res) => {
          // let newName = res.data.data.Full_Name;
          // setName(newName);
          setSelectedRowKeys(conditions[index].key);
          setSelectedRows(res.data.data);
          setVisible(false);
          let fieldValue = [];
          res.data.data.map((item, idx) => {
            fieldValue.push(
              `${item.First_Name} ${item.Middle_Name} ${item.Last_Name}`
            );
            return null;
          });
          let tmp = [...value];
          tmp[index] = fieldValue;
          setValue(tmp);
        })
        .catch((err) => console.log(err));
    }
    /* eslint-disable-next-line */
  }, [ID, conditions, checkTokenExpiration, init]);

  const reloadData = (current_page, record_per_page, searchData) => {
    dispatch(
      loadUser({
        current_page: current_page,
        record_per_page: record_per_page,
        search_data: searchData,
      })
    );
  };

  const handleSelect = (selectedRowKeys, selectedRows) => {
    if (operatorValue !== "$nin" && operatorValue !== "$in") {
      let newName = `${selectedRows[0].First_Name} ${selectedRows[0].Middle_Name} ${selectedRows[0].Last_Name}`;
      setName(newName);
      setVisible(false);

      let tmpConditions = [...conditions];

      let newTmpConditions = [];
      /* eslint-disable-next-line */
      tmpConditions.forEach((item, idx) => {
        newTmpConditions[idx] = { ...item };
      });

      newTmpConditions[index].value = {
        [operatorValue]: selectedRowKeys[0],
      };
      newTmpConditions[index].key = selectedRowKeys[0];
      setConditions(newTmpConditions);

      let tmp = [...value];
      tmp[index] = newName;
      setValue(tmp);
    } else {
      setVisible(false);

      let tmpConditions = [...conditions];

      let fieldValue = [];
      selectedRows.map((item, index) => {
        fieldValue.push(
          `${item.First_Name} ${item.Middle_Name} ${item.Last_Name}`
        );
        return null;
      });
      tmpConditions[index] = {
        ...tmpConditions[index],
        value: {
          ...tmpConditions[index].value,
          [operatorValue]: selectedRowKeys,
        },
      };

      tmpConditions[index] = {
        ...tmpConditions[index],
        key: selectedRowKeys,
      };
      setConditions(tmpConditions);

      let tmp = [...value];

      tmp[index] = [...new Set([...fieldValue])];
      setValue(tmp);
    }
  };

  useEffect(() => {
    let tmp = [];
    let tmpConditions = [];
    /*eslint-disable-next-line*/
    conditions.map((item, idx) => {
      tmpConditions.push({ ...item });
    });
    /*eslint-disable-next-line*/
    value.map((item, idx) => {
      tmp.push(item);
    });

    switch (operatorValue) {
      case "empty":
        tmpConditions[index].value = {
          $eq: null,
        };

        tmp[index] = "";
        setValue(tmp);

        setConditions(tmpConditions);
        break;
      case "not-empty":
        tmpConditions[index].value = {
          $ne: null,
        };
        tmp[index] = "";
        setValue(tmp);
        setConditions(tmpConditions);
        break;
      case "mine":
        tmpConditions[index].value = {
          $eq: "mine",
        };
        tmp[index] = "";
        setValue(tmp);
        setConditions(tmpConditions);
        break;
      case "not-mine":
        tmpConditions[index].value = {
          $ne: "mine",
        };
        tmp[index] = "";
        setValue(tmp);
        setConditions(tmpConditions);
        break;

      default:
        break;
    }

    /*eslint-disable-next-line*/
  }, [operatorValue]);

  console.log(
    "hihii",
    operatorValue === "$nin" || operatorValue === "$in"
      ? typeof value[index] === "object" && value[index]
      : ""
  );

  console.log("value", value[index], index);

  return (
    <>
      <Input
        disabled={
          disabled ||
          operatorValue === "empty" ||
          operatorValue === "not-empty" ||
          operatorValue === "mine" ||
          operatorValue === "not-mine"
        }
        addonAfter={
          <>
            {disabled ||
            operatorValue === "empty" ||
            operatorValue === "not-empty" ||
            operatorValue === "mine" ||
            operatorValue === "not-mine" ? (
              <SearchOutlined style={{ pointerEvents: "none" }} />
            ) : (
              <SearchOutlined
                onClick={() => {
                  setVisible(true);
                }}
              />
            )}
          </>
        }
        addonBefore={
          disabled ||
          operatorValue === "empty" ||
          operatorValue === "not-empty" ||
          operatorValue === "mine" ||
          operatorValue === "not-mine" ? (
            ""
          ) : name ? (
            <ClearOutlined
              onClick={() => {
                setName("");
                let tmpConditions = [...conditions];

                let newTmpConditions = [];
                /* eslint-disable-next-line */
                tmpConditions.forEach((item, idx) => {
                  newTmpConditions[idx] = { ...item };
                });

                newTmpConditions[index].value = {
                  [operatorValue]: "",
                };
                newTmpConditions[index].key = "";
                setConditions(newTmpConditions);

                let tmp = [...value];
                tmp[index] = "";
                setValue(tmp);
              }}
            />
          ) : (
            ""
          )
        }
        value={name}
      />
      {operatorValue === "$nin" || operatorValue === "$in"
        ? typeof value[index] === "object" &&
          value[index]?.map((item, idx) => {
            return (
              <CustomTag
                closable
                onClose={() => {
                  // let tmpRowKeys = [...selectedRowKeys];

                  let tmpConditions = [...conditions];
                  let tmp = [...value];
                  let flag;
                  // setSelectedRowKeys(tmpRowKeys);

                  tmpConditions[index] = {
                    ...tmpConditions[index],
                    value: {
                      ...tmpConditions[index].value,
                      [operatorValue]: value[index].filter((ele, indexx) => {
                        if (ele === item) {
                          flag = item;
                        }
                        return ele !== item;
                      }),
                    },
                  };
                  let tempKeys =
                    typeof tmpConditions[index].key === "object"
                      ? tmpConditions[index].key
                      : [];

                  let tempKeysNew = [];
                  let selectedRowsNew = [];
                  tempKeys.forEach((ele, indexx) => {
                    selectedRows.forEach((element, indexxx) => {
                      if (element._id === ele) {
                        if (
                          flag !==
                          `${element.First_Name} ${element.Middle_Name} ${element.Last_Name}`
                        ) {
                          tempKeysNew.push(ele);
                          selectedRowsNew.push(element);
                        }
                      }
                    });
                  });

                  tmpConditions[index] = {
                    ...tmpConditions[index],
                    key: tempKeysNew,
                    value: {
                      [operatorValue]: tempKeysNew,
                    },
                  };

                  setSelectedRows(selectedRowsNew);
                  setSelectedRowKeys(tempKeysNew);
                  setConditions(tmpConditions);
                  tmp[index] = value[index].filter((ele) => ele !== item);
                  setValue(tmp);
                }}
              >
                {item}
              </CustomTag>
            );
          })
        : ""}
      <UserList
        visible={visible}
        setVisible={setVisible}
        users={users}
        selectedRowKeys={selectedRowKeys}
        setSelectedRowKeys={setSelectedRowKeys}
        reloadData={reloadData}
        handleSelect={handleSelect}
        selectedRows={selectedRows}
        setSelectedRows={setSelectedRows}
        searchData={searchData}
        setSearchData={setSearchData}
        conditions={conditions}
        index={index}
        ID={ID}
        operatorValue={operatorValue}
      />
    </>
  );
};

export default User;
const CustomTag = styled(Tag)`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  text-align: center;
  width: 100% !important;
  height: fit-content !important;
  margin-bottom: 0px !important;
  font-size: 12px !important;
  padding: 0 !important;
  .ant-tag {
    padding: 20px;
  }
`;
